$primario:#fbdc1f;
$secundario:#274ba6;
.primario{
	color:$primario !important;
}
.bgprimario{
	background-color:$primario !important;
}
.secundario{
	color:$secundario !important;
}
.bgsecundario{
	background-color:$secundario !important;
}
.blanco{
	color: #FFFFFF;
}
.bgblanco{
	background-color: #FFFFFF;
}

.center-absolute{
	position:absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%,-50%);
}

#entrar-background{
	position:relative;
	width:100vw;   
	height: calc(100vh - 42px);
	background-size:cover;
	background-position:center;
	background-repeat:no-repeat;
	min-height:500px;
	#box-ingresar{
		-moz-background-clip: padding;     /* Firefox 3.6 */
		-webkit-background-clip: padding;  /* Safari 4? Chrome 6? */
		background-clip: padding-box;
		text-align: center;
		background-color:rgba(255,255,255,0.9);
		border:20px solid rgba(255,255,255,0.6);
		padding: 20px;
	}
}

#footer{
	background-color:$secundario;
	border-top:2px solid $primario;
}

hr{
	background-color:$secundario;
}


.btn{
	font-family:'TradeGothic BoldCon';
	border-radius:0px;
	cursor: pointer;
	&.btn-primario{
		background-color:$primario;
		color:$secundario;
		-webkit-box-shadow: 0 3px 5px 0 rgba(0,0,0,0.7);
		box-shadow: 0 3px 5px 0 rgba(0,0,0,0.7);
	}
}

#nav{
	border-bottom:2px solid $primario;
	.navbar-nav{
		li{
			a{
				i.fa{
					font-size: 26px !important;
				}
			}
		}
	}
}

#nav-usuario{
	background-image: url(../img/ingresar1.jpg);
	background-size: cover;
	background-position: center;
	background-repeat: none;

}

a{
	color:inherit !important;
}
a:hover{
	color:inherit !important;
	text-decoration:none !important;
}